	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"The Catch","date":"30 Sep 2004","slug":"/2004-09-30-the-catch/","tags":"Y2004"},"html":"<p><a href=\"http://www.nytimes.com/2004/09/29/sports/baseball/29vecsey.html?ex=1254196800&en=ce21e7814a388f17&ei=5090&partner=rssuserland\" title=\"The New York Times - Sports - Baseball - Sports of The Times: Hazy Sunshine, Vivid Memory\">50 years ago: The Catch</a> by Willie Mays in the 1954 World Series</p>"},"next":{"fields":{"title":"Tony Blair Does Monty Python","date":"29 Sep 2004","slug":"/2004-09-29-tony-blair-does-monty-python/"}},"previous":{"fields":{"title":"The Great Debate","date":"30 Sep 2004","slug":"/2004-09-30-the-great-debate/"}}},{"node":{"fields":{"title":"Tony Blair Does Monty Python","date":"29 Sep 2004","slug":"/2004-09-29-tony-blair-does-monty-python/","tags":"Y2004"},"html":"<p><a href=\"http://windsofchange.net/archives/005614.php\">Winds of Change.NET: Tony Blair Does Monty Python</a> at the Labour Party conference: \"And the daftest thing said about New Labour is not the usual 'What have the Romans ever done for us?'...\"</p>"},"next":{"fields":{"title":"Seeing Stars","date":"29 Sep 2004","slug":"/2004-09-29-seeing-stars/"}},"previous":{"fields":{"title":"The Catch","date":"30 Sep 2004","slug":"/2004-09-30-the-catch/"}}},{"node":{"fields":{"title":"Seeing Stars","date":"29 Sep 2004","slug":"/2004-09-29-seeing-stars/","tags":"Y2004"},"html":"<p>Interestingly, my post on <a href=\"http://www.papascott.de/archives/2004/09/27/stars/\" title=\"PapaScott - Stars\">Stars</a>  generated a number of mails from my non-virtual community, er, I mean from friends and relatives, with suggestions on where to find star stickers (with Christmas decorations) and wondering if I was really serious about reforming German education with sticky bits of glossy paper.</p>\n<p>So to clarify, the post was written with tongue in cheek. My experience with German education is zero, other than what is reported in the media, and all I really know is that children start first grade with a paper cone full of toys and candy, and come out 10 or 12 or 13 years later with an <em>Abitur</em> or an apprenticeship. I have no idea what happens in between.</p>\n<p>I use the writing in my weblog to experiment, to try new points of view, to strike poses, to distract myself from the real world. The viewpoints expressed here may have no basis in reality. I don't always tell the truth, but I never lie. The posts here are a mixture of fact, fiction and imagination, and the recipe is different every day. Enjoy the ride.</p>"},"next":{"fields":{"title":"Any Questions?","date":"29 Sep 2004","slug":"/2004-09-29-any-questions/"}},"previous":{"fields":{"title":"Tony Blair Does Monty Python","date":"29 Sep 2004","slug":"/2004-09-29-tony-blair-does-monty-python/"}}}],"pathPrefix":"","first":false,"last":false,"index":412,"pageCount":1101}}

